$bi-fonts-direction: "fonts/" !default;

@font-face {
    font-family: 'blueicon';
    src:    url('#{$bi-fonts-direction}blueicon.eot?gsd354');
    src:    url('#{$bi-fonts-direction}blueicon.eot?gsd354#iefix') format('embedded-opentype'),
        url('#{$bi-fonts-direction}blueicon.ttf?gsd354') format('truetype'),
        url('#{$bi-fonts-direction}blueicon.woff?gsd354') format('woff'),
        url('#{$bi-fonts-direction}blueicon.svg?gsd354#blueicon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="bi-"], [class*=" bi-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'blueicon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@import "style";

/* o-icons -- colorganize.com */
$blue: #4D82B8; 
$gray: #808080; 
$red: #E68497; 
$green: #76A797; 
$yellow: #EDC87E; 
$orange: #ed9b7e; 
$light_blue: #5ab5d6;

[class^="bi-"], [class*=" bi-"] {
    font-size: 1.15em;
    
    position: relative;
    top: 1px;
    display: inline-block;
}

.bi-c-orange {
    color: $orange;
}

.bi-c-green {
    color: $green;
}

.bi-c-red {
    color: $red
}

.bi-c-dark,
.bi-c-gray {
    color: $gray;
}

.bi-c-blue {
    color: $blue;
}

.bi-c-yellow {
    color: $yellow;
}

.bi-c-light-blue {
    color: $light_blue;
}

.bi-a-ringing {
    animation: ringing .25s linear infinite;
    -webkit-animation: ringing .25s linear infinite;
}

@keyframes ringing {
    0%		{ transform: rotate(0deg); }
    20%		{ transform: rotate(-30deg); }
    45%		{ transform: rotate(-5deg); }
    60%		{ transform: rotate(5deg); }
    75%		{ transform: rotate(0deg); }
    100%	{ transform: rotate(0deg); }
}

@-webkit-keyframes ringing {
    0%		{ transform: rotate(0deg); }
    20%		{ transform: rotate(-30deg); }
    45%		{ transform: rotate(-5deg); }
    60%		{ transform: rotate(5deg); }
    75%		{ transform: rotate(0deg); }
    100%	{ transform: rotate(0deg); }
}

.bi-a-spin {
    -webkit-animation:spin 4s linear infinite;
    -moz-animation:spin 4s linear infinite;
    animation:spin 4s linear infinite;    
}

@-moz-keyframes spin { 
    100% { 
        -moz-transform: rotate(-360deg); 
    } 
}
@-webkit-keyframes spin { 
    100% { 
        -webkit-transform: rotate(-360deg); 
    } 
}

@keyframes spin {
    100% { 
        -webkit-transform: rotate(-360deg); 
        transform:rotate(-360deg); 
    }
}